<template>
  <b-overlay :show="loading">
    <h1>Мои цели</h1>
    <b-row>
      <b-col v-for="item in items" :key="item.id" xl="4" lg="6">
        <b-card
          @click="openGoal(item.id)"
          :title="item.title"
          img-src="https://via.placeholder.com/350x200"
          img-alt="Image"
          img-top
          tag="article"
          class="m-4 card"
        >
        </b-card>
      </b-col>
    </b-row>
  </b-overlay>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "MyGoals",
  data() {
    return {
      loading: false,
    };
  },
  mounted() {
    this.loading = true;
    this.$store.dispatch("goalsItems").finally(() => {
      this.loading = false;
    });
  },
  computed: mapState({
    items: (state) => state.goalsItems,
  }),
  methods: {
    openGoal: function (id) {
      this.$router.push({ name: "ViewGoal", params: { id: id } });
    },
  },
};
</script>

<style lang="scss" scoped>
.card {
  cursor: pointer;
}
</style>
