<template>
  <b-overlay :show="loading">
    <div v-if="item">
      <h1 class="mb-5">{{ item.title }}</h1>
      <h4>Критерий завершения</h4>
      <p class="mb-4">{{ item.endCriterion }}</p>
      <h4>Описание</h4>
      <div v-html="item.description" class="mb-4"></div>
      <h4>Список задач</h4>
      <ul class="todo-list">
        <li
          class="todo-item"
          :class="{ 'todo-item-checked': todo.checked }"
          :key="todo.title"
          v-for="todo in item.todo"
        >
          <i v-if="!todo.checked" class="far fa-square"></i>
          <i v-else class="far fa-check-square"></i>
          {{ todo.title }}
        </li>
      </ul>
      <b-button
        v-if="
          item.user_id === null || item.user_id === $store.state.profileItem.id
        "
        class="mt-5 mr-2"
        @click="
          $router.push({ name: 'EditGoal', params: { id: id, item: item } })
        "
        variant="primary"
        >Редактировать</b-button
      >
      <b-button
        v-if="
          item.user_id === null || item.user_id === $store.state.profileItem.id
        "
        class="mt-5"
        @click="deleteGoal"
        variant="danger"
        >Удалить</b-button
      >
    </div>
  </b-overlay>
</template>

<script>
import axios from "axios";
export default {
  name: "Goal",
  props: ["id"],
  data() {
    return {
      loading: false,
      item: null,
    };
  },
  mounted() {
    this.loadItem();
  },
  methods: {
    loadItem: function () {
      this.loading = true;
      axios
        .get("/api/goals/item/" + this.id)
        .then((response) => {
          this.item = response.data[0];
          this.item.todo = JSON.parse(this.item.todo);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    deleteGoal: function () {
      this.$swal({
        title: "Удаление цели",
        text: "Вы уверены, что хотите удалить цель?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        confirmButtonText: "Удалить",
        cancelButtonText: "Отмена",
      }).then((result) => {
        if (result.isConfirmed) {
          axios.delete("/api/goals/delete/" + this.id).then(() => {
            this.$swal({
              title: "Успех",
              text: "Цель успешно удалена",
              icon: "success",
            }).then(() => {
              this.$router.push({ name: "MyGoals" });
            });
          });
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.todo-list {
  list-style: none;
  margin-left: 25px;
  padding: 0;
}

.todo-item-checked {
  text-decoration: line-through;
}
</style>
