import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isAuth: false,
    user: null,
    hideContent: false,
    profileItem: null,
    goalsItems: [],
    allGoals: [],
  },
  mutations: {
    setUser(state, data) {
      state.user = data;
    },
    setAuth(state, data) {
      state.isAuth = data;
    },
    setProfileItem(state, data) {
      state.profileItem = data;
    },
    setHideContent(state, data) {
      state.hideContent = data;
    },
    setGoalsItems(state, data) {
      state.goalsItems = data;
    },
    setAllGoals(state, data) {
      state.allGoals = data;
    },
  },
  getters: {
    getUser(state) {
      return state.user;
    },
    getProfileItem(state) {
      return state.profileItem;
    },
  },
  actions: {
    authUser({ commit }, data) {
      return new Promise((resolve, reject) => {
        commit("setHideContent", true);
        axios
          .post("/api/auth/login", data)
          .then((response) => {
            const data = response.data;
            Vue.prototype.$setCookie(
              "jwt",
              data.access_token,
              data.expires_in * 10
            );
            commit("setAuth", true);
            commit("setUser", data.user);
            Vue.prototype.$getCookie();
            resolve();
          })
          .catch(() => {
            reject();
          })
          .finally(() => {
            commit("setHideContent", false);
          });
      });
    },
    registerUser({ commit }, data) {
      return new Promise((resolve, reject) => {
        commit("setHideContent", true);
        axios
          .post("/api/auth/register", data)
          .then(() => {
            resolve();
          })
          .catch(() => {
            reject();
          })
          .finally(() => {
            commit("setHideContent", false);
          });
      });
    },
    logoutUser({ commit }) {
      return new Promise((resolve, reject) => {
        commit("setHideContent", true);
        axios
          .post("/api/auth/logout")
          .then(() => {
            resolve();
          })
          .catch(() => {
            reject();
          })
          .finally(() => {
            commit("setUser", null);
            commit("setAuth", false);
            Vue.prototype.$setCookie("jwt", null, 0);
            commit("setHideContent", false);
          });
      });
    },
    profileItem({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .get("/api/profile/item")
          .then((response) => {
            commit("setProfileItem", response.data);
            resolve();
          })
          .catch(() => {
            reject();
          });
      });
    },
    editProfileItem(_, data) {
      return new Promise((resolve, reject) => {
        axios
          .post("/api/profile/edit", data)
          .then(() => {
            resolve();
          })
          .catch(() => {
            reject();
          });
      });
    },
    goalsItems({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .get("/api/goals/useritems")
          .then((response) => {
            commit("setGoalsItems", response.data);
            resolve();
          })
          .catch(() => {
            reject();
          });
      });
    },
    loadAllGoals({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .get("/api/goals/allitems")
          .then((response) => {
            commit("setAllGoals", response.data);
            resolve();
          })
          .catch(() => {
            reject();
          });
      });
    },
  },
  modules: {},
});
