<template>
  <div id="app">
    <b-overlay class="center-overlay" :show="hideContent">
      <template v-if="!isAuth">
        <div class="navbar" id="nav">
          <b-link class="nav-link" :to="{ name: 'Home' }"> Главная </b-link>
          <b-button
            v-if="$route.name !== 'Login'"
            class="nav-link"
            variant="outline-primary"
            @click="$router.push({ name: 'Login' })"
          >
            Вход
          </b-button>
        </div>
      </template>
      <template v-else>
        <aside class="sidebar" id="sidebar">
          <b-link class="side-link" :to="{ name: 'App' }">
            <div class="side-icon-wrapper">
              <i class="fas fa-home side-icon"></i>
            </div>
            <span> Главная </span>
          </b-link>
          <b-link class="side-link" :to="{ name: 'Profile' }">
            <div class="side-icon-wrapper">
              <i class="fas fa-user side-icon"></i>
            </div>
            <span> Личный кабинет </span>
          </b-link>
          <b-link class="side-link" :to="{ name: 'MyGoals' }">
            <div class="side-icon-wrapper">
              <i class="fas fa-bullseye side-icon"></i>
            </div>
            <span> Мои цели </span>
          </b-link>
          <b-link class="side-link" :to="{ name: 'CreateGoal' }">
            <div class="side-icon-wrapper">
              <i class="fas fa-plus-circle"></i>
            </div>
            Создать цель
          </b-link>
          <a class="side-link last-button" @click="toggleSidebar">
            <div class="side-icon-wrapper side-icon-arrow">
              <i class="fas fa-chevron-left side-icon"></i>
            </div>
            <span> Скрыть панель </span>
          </a>
          <b-link class="side-link" @click="doLogout">
            <div class="side-icon-wrapper">
              <i class="fas fa-sign-out-alt side-icon"></i>
            </div>
            <span> Выйти </span>
          </b-link>
        </aside>
      </template>
      <div v-if="!hideContent" id="main" :class="{ main: isAuth }">
        <b-container>
          <b-row>
            <b-col cols="12">
              <router-view />
            </b-col>
          </b-row>
        </b-container>
      </div>
    </b-overlay>
  </div>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import { mapState } from "vuex";

export default {
  computed: mapState({
    hideContent: (state) => state.hideContent,
    isAuth: (state) => state.isAuth,
  }),
  created() {
    axios.defaults.baseURL = "https://apicodinggoals.herokuapp.com";

    axios.interceptors.request.use(
      (config) => {
        const token = Vue.prototype.$getCookie("jwt");
        if (config.url[config.url.length - 1] === "/") {
          config.url = config.url.substr(0, config.url.length - 1);
        }
        if (token != null) {
          config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
      },
      (error) => {
        console.log(error);
        if (
          error.response !== undefined &&
          error.response !== null &&
          error.response.status !== undefined
        ) {
          if (401 === error.response.status || 419 === error.response.status) {
            this.$store.commit("setUser", null);
            this.$store.commit("setAuth", false);
            this.$setCookie("jwt", null, 0);
          }
        }
        return error;
      }
    );

    axios.interceptors.response.use(
      (response) => {
        return response;
      },
      (error) => {
        if (
          error.response !== undefined &&
          error.response !== null &&
          error.response.status !== undefined
        ) {
          if (401 === error.response.status || 419 === error.response.status) {
            this.$store.commit("setUser", null);
            this.$store.commit("setAuth", false);
            this.$setCookie("jwt", null, 0);
          }
        }
        return error;
      }
    );

    const token = this.$getCookie("jwt");
    if (token != null) {
      this.$store.commit("setAuth", true);
      this.$store.commit("setHideContent", true);
      this.$store.dispatch("profileItem").finally(() => {
        this.$store.commit("setHideContent", false);
      });
    }
  },
  methods: {
    doLogout: function () {
      this.$swal({
        title: "Выход из аккаунта",
        text: "Вы уверены, что хотите выйти из аккаунта?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        confirmButtonText: "Выйти",
        cancelButtonText: "Отмена",
      }).then((result) => {
        if (result.isConfirmed) {
          this.$store.dispatch("logoutUser").finally(() => {
            this.$router.push({ name: "Login" });
          });
        }
      });
    },
    toggleSidebar: function () {
      const sidebar = document.getElementById("sidebar");
      const main = document.getElementById("main");

      main.classList.toggle("full-main");
      sidebar.classList.toggle("sidebar-little");
    },
  },
};
</script>

<style lang="scss">
@import "assets/scss/vendors/bootstrap-vue/index";
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
@import "~sweetalert2/src/variables";
@import "~sweetalert2/src/sweetalert2";

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 80px;
  padding: 0 20px;
  border-bottom: 1px solid lightgray;
}

.main {
  min-height: 100vh;
  z-index: 228;
  position: relative;
  padding: 15px 15px 15px 75px;
  @media (min-width: 768px) {
    padding: 15px 15px 15px 235px;
  }
  transition: all 0.5s;

  &.full-main {
    padding: 15px 15px 15px 75px !important;
  }

  & .container {
    min-height: 100%;
  }
}

.sidebar {
  z-index: 999;
  position: fixed;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 220px;
  background: black;
  border: 2px solid black;
  transition: all 0.5s;

  &.sidebar-little {
    width: 60px;

    & .side-icon-arrow svg {
      transform: rotateZ(180deg);
    }
  }
}

.last-button {
  margin-top: auto;
}

.side-link {
  display: flex;
  align-items: center;
  border-radius: 0;
  width: 100%;
  color: rgb(255, 255, 255);
  background: black;
  border-color: black;
  height: 60px;
  white-space: nowrap;
  padding-left: 20px;
  cursor: pointer;

  &:hover {
    color: white;
    text-decoration: none;
    border-color: rgb(12, 11, 11);
    background: rgb(12, 11, 11);
  }
}

.side-icon {
  display: inline-block;
}

.side-icon-arrow svg {
  transform: rotateZ(0deg);
  transition: all 0.5s;
}

.side-icon-wrapper {
  flex-grow: 0;
  flex-shrink: 0;
  width: 20px;
  height: 24px;
  margin-right: 25px;
  transition: all 1s;
}

#app {
  position: relative;
}
</style>
