<template>
  <b-overlay :show="loading">
    <h1>Профиль</h1>
    <div class="d-flex flex-column align-items-center justify-content-center">
      <div class="avatar">
        <img
          src="https://communityevolution.org/wp-content/uploads/2020/02/missing-profile-picture-1.jpg"
        />
      </div>
      <h2 class="pt-2">
        <span>{{ item.name }} {{ item.surname }} {{ item.patro }}</span>
      </h2>
      <b-row class="user-info container-fluid d-flex justify-content-around">
        <b-col xl="5" class="d-flex justify-content-between">
          <span> Имя: {{ item.name }} </span>
          <span @click="openEditModal('name')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
        <b-col xl="5" class="d-flex justify-content-between">
          <span>Фамилия: {{ item.surname }}</span>
          <span @click="openEditModal('surname')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
        <b-col xl="5" class="d-flex justify-content-between">
          <span>Отчество: {{ item.patro }}</span>
          <span @click="openEditModal('patro')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
        <b-col xl="5" class="d-flex justify-content-between">
          <span>Email: {{ item.email }}</span>
          <span @click="openEditModal('email')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
        <b-col xl="5" class="d-flex justify-content-between">
          <span>Страна: {{ item.country }}</span>
          <span @click="openEditModal('country')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
        <b-col xl="5" class="d-flex justify-content-between">
          <span>Город: {{ item.town }}</span>
          <span @click="openEditModal('town')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
        <b-col xl="5" class="d-flex justify-content-between">
          <span>Пол: {{ item.gender }}</span>
          <span @click="openEditModal('gender')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
        <b-col xl="5" class="d-flex justify-content-between">
          <span>День рождения: {{ item.birthday }}</span>
          <span @click="openEditModal('birthday')">
            <i class="fas fa-edit" />
          </span>
        </b-col>
      </b-row>
      <b-modal
        class="editModal"
        v-model="showEditModal"
        title="Хотите изменить информацию о себе?"
      >
        <div class="d-flex flex-column">
          <span>Введите новое значение</span>
          <b-input v-model="editField" type="text" />
        </div>
        <template #modal-footer>
          <div class="d-flex flex-end">
            <b-button
              size="md"
              class="float-right"
              @click="showEditModal = false"
            >
              Отмена
            </b-button>
            <b-button size="md" class="float-right" @click="edit">
              Сохранить
            </b-button>
          </div>
        </template>
      </b-modal>
    </div>
  </b-overlay>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Profile",
  data() {
    return {
      editField: "",
      editData: null,
      showEditModal: false,
      loading: false,
    };
  },
  mounted() {
    if (this.item === null) {
      this.loading = true;
      this.$store.dispatch("profileItem").finally(() => {
        this.loading = false;
      });
    }
  },
  computed: mapState({
    item: (state) => state.profileItem,
  }),
  methods: {
    openEditModal(name) {
      this.editData = name;
      this.editField = this.item[name];
      this.showEditModal = true;
    },
    edit() {
      const obj = {};
      obj[this.editData] = this.editField;
      this.item[this.editData] = this.editField;
      this.$store.dispatch("editProfileItem", obj);
      this.showEditModal = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.avatar {
  border-radius: 10%;
  border: 5px solid black;
  margin-top: 10vh;
  overflow: hidden;
  @media (max-width: 768px) {
    margin-top: 5vh;
  }
}

.avatar img {
  width: 200px;
}

.user-info {
  margin-top: 25px;
  div {
    display: flex;
    align-items: center;
    height: 75px;
    padding: 15px;
    border: 5px solid black;
    margin: 10px 5px;
    border-radius: 10px;
    @media (max-width: 768px) {
      border: 3px solid black;
      height: 50px;
    }
  }
}

span {
  font-size: 25px;
  font-weight: bold;
  padding: 0 3px;
  @media (max-width: 768px) {
    font-size: 14px;
  }
}

.modal {
  button {
    background: black;
    color: white;
    margin: 5px 0 5px 10px;
  }
  span {
    font-size: 16px;
    padding: 10px 0;
  }
}
</style>
